(function() {
  'use strict';

  angular
    .module('csport')
    .directive('swiper', swiper);

  /** @ngInject */
  function swiper() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/swiper/swiper.html',
      controller: SwiperController,
      controllerAs: 'SS'
    };

    return directive;
    /** @ngInject */
    function SwiperController(send, $log) {
      var vm = this;
      vm.posts = [];

      send.request('/post/top', 'GET')
        .then(function(res) {
          vm.posts = res;
        }, function(err) {
          $log.debug(err);
        })
    }
  }

})();

(function() {
  'use strict'

  angular
      .module('csport')
      .service('send', Send)

  /** @ngInject */
  function Send($http, $log, config) {
    var prefix = '';
    if(config.env === 'dev')        prefix += config.dev.host + "/api";
    else                            prefix += config.app.host + "/api";

    var request = function(route, method, data, image){
      $log.debug('********Request*********')
      data ? $log.debug('%s %s %s',route, method, data)
      :      $log.debug('%s %s',route, method)


      if (!method){
        method = 'GET'
      }
      if (!image)
        return $http({
                  url: prefix + route,
                  method: method,
                  withCredentials: true,
                  data: data,
                  cache: false
                })
                  .then(function(response){
                    $log.debug("------Response-------\n%s",route,response.data);
                    return response.data;
                  })
      if (image){
        var fd = new FormData();
        $log.debug('data', data);
        for(var key in data){
          fd.append(key, data[key]);
        }
        $log.debug('fd',fd);
        return $http( {
          url: prefix + route,
          method: method,
          data: fd,
          // }, {
          headers: {
            'Content-Type': undefined
          },
          transformRequest: angular.identity
        })
          .then(function(response){
            return response.data;
          })
      }
    }

    return {
      request: request
    };
  }
})();

(function() {
  'use strict'
  angular
      .module('csport')
      .filter('limitHtml', function() {
        return function(text, limit) {

            var changedString = String(text).replace(/<[^>]+>/gm, '');

            return changedString.length > limit ? changedString.substr(0, limit - 1) : changedString;
        }
      })
      .filter('timeFilter', function() {
        return function(millseconds) {
          var seconds = Math.floor(millseconds / 1000);
          var days = Math.floor(seconds / 86400);
          var hours = Math.floor((seconds % 86400) / 3600);
          var minutes = Math.floor(((seconds % 86400) % 3600) / 60);
          var timeString = '';
          if(days > 0) timeString += (days > 1) ? (days + "d ") : (days + "d ");
          if(hours > 0) timeString += (hours > 1) ? (hours + "h ") : (hours + "h ");
          if(days < 1 && minutes >= 0) timeString += (minutes > 1) ? (minutes + "m ") : (minutes + "m ");
          return timeString;
      }
      });
})();

(function() {
  'use strict';

  angular
    .module('csport')
    .controller('PostController', PostController);

  /** @ngInject */
  function PostController(send, $log, $stateParams) {
    var vm = this;
    vm.post = {};
    send.request('/post/' + $stateParams.id, 'GET')
      .then(function(res) {
        vm.post = res;
      }, function(err) {
        $log.debug(err);
      })
  }
})();

(function() {
  'use strict';

  angular
    .module('csport')
    .controller('HomeController', HomeController);

  /** @ngInject */
  function HomeController(config, send, $log) {
    var vm = this;
    vm.posts = [];
    vm.tours = [];
    vm.popular = [];

    vm.get = function(index){
      return vm.posts[index].content;
    }
    send.request('/post', 'GET')
      .then(function(res) {
        vm.posts = res;
      }, function(err) {
        $log.debug(err);
      })

    send.request('/tournament', 'GET')
      .then(function(res) {
        vm.tours = res;
      }, function(err) {
        $log.debug(err);
      })

      send.request('/post/popular', 'GET')
        .then(function(res) {
          vm.popular = res;
        }, function(err) {
          $log.debug(err);
        })

  }

})();

(function() {
  'use strict';

  angular
    .module('csport')
    .directive('match', match);

  /** @ngInject */
  function match() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/match/match.html',
      controller: MatchListController,
      controllerAs: 'Mlist'
    };

    return directive;

    /** @ngInject */
    function MatchListController(send, $log, moment) {
      var vm = this;
      vm.matches = [];

      send.request('/match/list', 'GET').then(function(res){
        vm.matches = res;
        angular.forEach(vm.matches, function(entry, index){

          var now = moment();
          var time = moment(entry.date);
          vm.matches[index].timeLeft = now.diff(time);
        })
      }, function(err){
        $log.debug(err);
      })
    }
  }

})();

